.container {
    margin-top: 0;
    margin-bottom: 0;
    margin-right: auto;
    margin-left: auto;
    padding-left: 20px;
    padding-right: 20px;
    

    @media screen and (min-width: 480px) {
        width: 480px;
        padding-left: 20px;
        padding-right: 20px;
        
    }

    @media screen and (min-width:768px) {
        width: 768px;
        padding-left: 32px;
        padding-right: 32px;
    }

    @media screen and (min-width: 1280px) {
        width: 1280px;
        padding-left: 16px;
        padding-right: 16px;
    }
}
