.list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.line {
    text-decoration: none;
}

ul,
li,
a {
    text-decoration: none;
    list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}



img {
    display: block;
    max-width: 100%;
    height: auto;

    object-fit: cover;
    object-position: center;
}